<template lang="pug">
a(:href="link" target="_blank" rel="noreferrer noopener" @mouseover="e => {setProjectFunction(title); animationEnterFunction(e)}" @mouseleave="e => animationLeaveFunction(e)") {{ title }}
  <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
    <path d="M5.8335 14.1666L14.1668 5.83331" stroke-width="2"></path>
    <path d="M5.8335 5.83331H14.1668V14.1666" stroke-width="2"></path>
  </svg>
</template>

<script>
export default {
  name: 'Link',
  props: {
    link: String,
    title: String,
    setProjectFunction: Function,
    animationEnterFunction: Function,
    animationLeaveFunction: Function,
  },
  setup() {
    function log(e) {
      if (e.offsetY >= (e.target.offsetHeight / 2)) {
        console.log("bottom")
      } else {
        console.log("top")
      }
    }

    function logLeave(e) {
      if (e.offsetY >= (e.target.offsetHeight / 2)) {
        console.log("leave: bottom")
      } else {
        console.log("leave: top")
      }
    }

    return {
      log,
      logLeave
    }
  }
}
</script>

<style lang="scss" scoped>
a {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 1.2rem;
  font-weight: 500;
  text-decoration: none;
  text-transform: uppercase;
  color: #808080;
  padding-top: 20px;
  padding-right: 90px;
  padding-bottom: 6px;
  border-bottom: 1px dotted #808080;
  transition: color 150ms ease-in-out;
  z-index: 100;

  &:hover {
    color: #FDD3B4;
    border-bottom-color: #FDD3B4;

    svg {
      stroke: #FDD3B4;
      transform: translateX(10px) translateY(-5px);
    }
  }

  svg {
    position: absolute;
    right: 0;
    stroke: #808080;
    transition: transform 150ms ease-in-out;
    pointer-events: none;
  }
}
</style>