<template lang="pug">
Noise
main
  .left
    .title
      h1.name SheezChill
      p.subtitle Aspiring Web Dev
    .link-list
      p PERSONAL PROJECTS
      Link(
        v-for="({ link, title, description, dateStarted }, index) in projects"
        :link="link"
        :title="title"
        :setProjectFunction="setProject"
        :animationEnterFunction="setAnimationEnter"
        :animationLeaveFunction="setAnimationLeave"
        :key="index"
      )
  .right
    .project
      .date-started
        h3(:class="{ show2: animation === 'topt', show1: animation === 'bottomt', hide1: animation === 'bottoml', hide2: animation === 'topl' }") DATE STARTED: {{ currentProject.dateStarted }}
      .title
        h1(:class="{ show2: animation === 'topt', show1: animation === 'bottomt', hide1: animation === 'bottoml', hide2: animation === 'topl' }") {{ currentProject.title }}
      .description
        h2(:class="{ show2: animation === 'topt', show1: animation === 'bottomt', hide1: animation === 'bottoml', hide2: animation === 'topl' }") {{currentProject.description}}
    .tech
      .label
        h3(:class="{ show2: animation === 'topt', show1: animation === 'bottomt', hide1: animation === 'bottoml', hide2: animation === 'topl' }") TECHNOLOGY
      .tech-list
        div(:class="{ show2: animation === 'topt', show1: animation === 'bottomt', hide1: animation === 'bottoml', hide2: animation === 'topl' }")
          p(
            v-for="tech in currentProject.tech"
          ) {{tech}}
</template>

<script>
import { reactive, ref } from '@vue/reactivity'
import Link from './components/Link.vue'
import Noise from './components/Noise.vue'

export default {
  name: 'App',
  components: {
    Link,
    Noise
  },
  setup() {
    const animation = ref();

    const currentProject = reactive({
      dateStarted: "Dec 25 2020",
      title: "VTrack",
      description: "Hololive Stream Tracker",
      tech: [
        "html5",
        "scss",
        "vue.js",
      ]
    })

    const projects = {
      "Stress Generator": {
        link: "https://stress-gen.vercel.app/",
        title: "Stress Generator",
        description: "A Joke Copypasta Generator",
        dateStarted: "Jan 21 2021",
        tech: ["html5", "scss", "react.js"]
      },
      "VTrack": {
        link: "https://vuetrack.vercel.app/",
        title: "VTrack",
        description: "Hololive Stream Tracker",
        dateStarted: "Dec 25 2020",
        tech: ["html5", "scss", "vue.js"]
      },
      "December Calendar": {
        link: "https://sheezchill.github.io/dec-2020s/",
        title: "December Calendar",
        description: "The Date But in December",
        dateStarted: "Jan 1 2021",
        tech: ["html5", "scss", "javascript"]
      },
    }

    function setProject(projectTitle) {
      const { title, description, dateStarted, tech } = projects[projectTitle]
      currentProject.dateStarted = dateStarted
      currentProject.title = title
      currentProject.description = description
      currentProject.tech = tech
    }

    function setAnimationEnter(e) {
      if (e.offsetY >= (e.target.offsetHeight / 2)) {
        animation.value = "bottomt"
      } else {
        animation.value = "topt"
      }
    }

    function setAnimationLeave(e) {
      if (e.offsetY >= (e.target.offsetHeight / 2)) {
        animation.value = "bottoml"
      } else {
        animation.value = "topl"
      }
    }

    return {
      projects,
      currentProject,
      setProject,
      setAnimationEnter,
      setAnimationLeave,
      animation
    }
  }
}
</script>

<style lang="scss">
:root {
  font-size: 10px;
}

* {
  margin: 0;
  padding: 0;
}

#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  align-items: center;
  height: 100vh;
  margin: 0;
  padding: 0;
  user-select: none;
  background: #121212;
}

html {
  background: #121212;

  &::before {
    content: "";
    z-index: -1;
    position: fixed;
    height: 100vh;
    width: 100vw;
  }
}

body {
  background: #121212;
  color: white;
  margin: 0;
  padding: 0;
}

main {
  font-family: "Inter", sans-serif;
  position: relative;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  place-items: center;
  background: #121212;
  color: #fff;
  width: 100vw;

  @media (max-width: 1023px) {
    grid-template-columns: 1fr;
  }

  .left {
    display: flex;
    flex-direction: column;
    gap: 60px;
  }

  .right {
    display: flex;
    justify-self: left;
    flex-direction: column;
    align-self: baseline;
    gap: 20px;

    @media (max-width: 1023px) {
      display: none;
    }
    
    div {
      overflow: hidden;
    }

    .project {
      display: flex;
      flex-direction: column;

      .date-started {
        margin-bottom: 6px;

        h3 {
          font-size: 1.2rem;
          font-weight: 500;
          color: #808080;
          text-transform: uppercase;
          transform: translateY(-180%);
        }
      }

      .description {
        margin-top: 12px;

        h2 {
          font-size: 1.8rem;
          font-weight: 400;
          color: #999;
          transform: translateY(-180%);
        }
      }

      .title {

        h1 {
          font-size: 2.4rem;
          font-weight: 500;
          color: #FDD3B4;
          transform: translateY(-180%);
        }
      }
    }

    .tech {
      
      .label {
        margin-bottom: 10px;

        h3 {
          transform: translateY(-180%);
        }
      }

      &-list {

        div {
          display: flex;
          flex-direction: row;
          gap: 12px;
          font-size: 1.2rem;
          color: #999;
          text-transform: uppercase;
          transform: translateY(-180%);
        }
      }
    }
  }
}

.title {
  line-height: 1;

  h1 {
   font-size: 2.4rem;
   font-weight: 500; 
  }

  p {
    font-size: 1.8rem;
    color: #999;
    margin-top: 6px;
  }
}

.link-list {
  display: flex;
  flex-direction: column;

  p {
    font-size: 1.2rem;
    font-weight: 500;
  }
}

.show1 {
  // transform: translateY(0%) !important;
  animation-name: show1;
  animation-duration: 150ms;
  animation-fill-mode: forwards;
}

.show2 {
  // transform: translateY(-180%) !important;
  animation-name: show2;
  animation-duration: 150ms;
  animation-fill-mode: forwards;
}

.hide1 {
  // transform: translateY(-180%) !important;
  animation-name: hide1;
  animation-duration: 150ms;
  animation-fill-mode: forwards;
}

.hide2 {
  // transform: translateY(-180%) !important;
  animation-name: hide2;
  animation-duration: 150ms;
  animation-fill-mode: forwards;
}

@keyframes show1 {
  0% {
    transform: translateY(180%);
    opacity: 0;
  }
  
  100% {
    transform: translateY(0%);
    opacity: 1;
  }
}

@keyframes show2 {
  0% {
    transform: translateY(-180%);
    opacity: 0;
  }

  100% {
    transform: translateY(0%);
    opacity: 1;
  }
}

@keyframes hide1 {
  0% {
    transform: translateY(0%);
    opacity: 1;
  }
  
  100% {
    transform: translateY(180%);
    opacity: 0;
  }
}

@keyframes hide2 {
  0% {
    transform: translateY(0%);
    opacity: 1;
  }
  
  100% {
    transform: translateY(-180%);
    opacity: 0;
  }
}
</style>
